*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kalam&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Signika:wght@500&display=swap');
header{
  height: 550px;
  display: flex;
}
#navbarSupportedContent{
  margin-left: 8rem;
}
#navbarSupportedContent ul li a{
  text-decoration: none;
  margin: 0px 8px;
  padding: 8px 28px;
 background-color: rgb(255, 255, 130);
  border-radius: 10px;
  color: black;
}
#navbarSupportedContent ul li a:hover{
  color: red;
  background-color: yellow;
}
nav{
  background-color: #0a0e5a;
  border-bottom: 1px solid;
}
.title{
  font-size: 33px;
  color: whitesmoke;
 font-family: 'Lobster', cursive;
 color: rgb(255, 255, 130);

}
.title:hover{
  color: whitesmoke;
}
.info{
  background-color: #0a0e5a;
  clip-path: polygon(0% 0px, 100% 0%, 69% 100%, 0% 100%);
  width: 100%;
  padding: 10px;
  height: 100%;
}
#myimg{
  width: 30%;
  border: 12px  double whitesmoke;
  position: absolute;
  border-radius: 100%;
  top: 18%;
  left: 70%;
}
.box{
  width: 90%;
  color: whitesmoke;
  height: 500px;
  padding: 23px;
}
.box h2{
  font-size: 30px;
}
.box h1{
  margin-top: 8px;
  font-size: 100px;
  line-height: 90px;
}
.box h3{
  margin-top: 5px;
  font-size: 33px;
  letter-spacing: 8px;
  word-spacing: 5px;
}
.box h4{
  width: 50%;
}
.box .name{
  text-shadow: 2px 4px 2px black;
   font-family: 'Kalam', cursive;
  color: rgb(255, 255, 130);
}
.main-clr{
  color: rgb(255, 255, 130);
  font-weight: bolder;
}
#about-container{
  border-radius: 10px;
  background-color: #0a0e5a;
  margin-top: 200px;
  padding: 23px;
}
#bio-data-content{
  background-color:#0a0e5a;
  padding: 18px;
  width: 50%;
  border-radius: 10px;
}
#bio-data-content h1{
  font-size: 70px;
  font-family: 'Signika', sans-serif;
}
#bio-data-content p{
  font-size: 20px;
  letter-spacing: 1px;
  word-spacing: 5px;
}
#bio-dataImg{
  border-radius: 100%;
  width: 450px;
}
#secondImage{
  width: 250px;
  border-radius: 100%;
  margin: 0 auto;
  display: none;
}
.resumeContainer{
  padding: 23px;
  border-radius: 15px;
  margin-top: 200px;
  display: flex;
  background-color: #0a0e5a;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}
.resumeContent h1{
  font-family: 'Signika', sans-serif;
}
.resumeContent{
  width:500px;
  margin: 3px 2px;
}
.resumeImg img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.resumeImg{
  width: 50%;
}


.skillsContainer{
  margin-top: 200px;
  border-radius: 15px;
  background-color: #0a0e5a;
  display: flex;
  flex-wrap: wrap;
  padding: 23px;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}
.skillsContainer div img{
  width: 100%;
  object-fit: cover;
}
.skillsContainer div h1{
  font-family: 'Signika', sans-serif;
  text-align: center;
}
.backend img{
  width: 100%;
  height: 40%;
}
.skillsContainer div{
  padding: 13px;
  background-color: whitesmoke;
  height: 500px;
  width: 330px;
  border-radius: 10px;

}

.frameworks img{
  height: 40%;
}

.socialMedia{
  background-color: whitesmoke;
  border-radius: 12px;
  width: 300px;
}
.contactContainer{
  margin-top: 150px;
  border-radius: 15px;
  background-color: #0a0e5a;
  padding: 23px;
  gap: 10px;
  display: flex;
  justify-content: space-between;
}
.social-icons{
  padding: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  
}
.social-icons a{
  text-decoration: none;
  color:#0a0e5a;
}
.social-icons a h2 i{
  margin-right: 15px;
}
.social-icons h2 {
  margin:20px 0px;
}
#socialMediaImg img{
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
}
#socialMediaImg {
  height: 300px;
}
@media only screen and (max-width: 1060px) {
  .info{
    clip-path:none;
  }
  #myimg{
   display: none;
  }
  #secondImage{
    display: block;
  }
  .resumeContainer{
    justify-content: center ;
    gap: 10px;
  }
  header{
    height: 850px;
  }
}
i{
  margin-right: 12px;
}
footer{
  height: 100px;
  margin-top: 150px;
  color: white;
  text-align: center;
  margin-bottom: 0;
  background-color: #0a0e5a;
}
@media only screen and (max-width: 990px) {
  #navbarSupportedContent ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap  ;
    justify-content: space-around;
    gap: 50px;
  }
  .box h4{
    width: 100%;
  }
  
}
@media only screen and (max-width: 800px) {
  #bio-dataImg{
    width: 400px;
  }
  .box{
    padding-top: 0;
    margin-top: 0;
    width: 100%;
  }
 
  
  #bio-data-content{
    width: 90%;
  }
  .resumeImg{
    width: 100%;
  }
  header{
    height: 900px;
  }
}